<template>
    <header class="header">
        <div class="header__container">
            <div class="header__left">
                <img src="./../assets/couple.svg" alt="couple" class="header__img">
                <div class="header__title">Приворожу любимого человека</div>
            </div>
            <ul class="menu" :class="{menu_tablet: tablet}" v-if="tablet ? active : 1">
                <li
                    v-for="(li, i) in menuList"
                    :key="i"
                >
                    <a href="#" @click="active = false, scroll(`#${li.link}`)">{{ li.title }}</a>
                </li>
            </ul>
            <div v-if="tablet" class="burger" @click="active = !active" :class="{burger_open: active}">
                <div class="burger__container">
                    <div class="burger__line"></div>
                    <div class="burger__line"></div>
                    <div class="burger__line"></div>
                </div>
            </div>
        </div>
    </header>
</template>
<script>

export default {
    name: "HeaderBlock",
    data() {
        return {
            active: false,
            tablet: window.innerWidth < 1024,
            menuList: [
                {
                    link: "main",
                    title: "Главная"
                },
                {
                    link: "why",
                    title: "Почему я?"
                },
                {
                    link: "love",
                    title: "Любовный приворот",
                },
                {
                    link: "about",
                    title: "Обо мне",
                },
                {
                    link: "services",
                    title: "Услуги",
                },
                {
                    link: "testimonials",
                    title: "Отзывы",
                },
                {
                    link: "contacts",
                    title: "Контакты",
                },
            ],
        };
    },
    methods: {
        getWidth() {
            this.tablet = window.innerWidth < 1024;
        },
        scroll(id) {
            console.log(document.querySelector(String(id)).offsetTop);
            window.scrollTo({top: document.querySelector(String(id)).offsetTop, behavior: "smooth"});
        }
    },
    mounted() {
        window.addEventListener("resize", this.getWidth);
    }
}
</script>
<style lang="scss" type="scss" scoped>
    .burger {
        position: absolute;
        right: 0;
        top: 20px;
        width: 30px;
        height: 20px;
        &__container {
            position: relative;
            width: 30px;
            height: 20px;
        }
        &__line {
            width: 100%;
            height: 4px;
            background-color: #fff;
            position: absolute;
            left: 0;
            &:nth-child(1) {
                top: 0;
            }
            &:nth-child(2) {
                top: calc(50% - 2px);
            }
            &:nth-child(3) {
                top: calc(100% - 4px);
            }
        }
        &_open {
            .burger__line {
                &:nth-child(1) {
                    top: calc(50% - 2px);
                    transform: rotate(-45deg);
                }
                &:nth-child(2) {
                    transform: rotate(45deg);
                }
                &:nth-child(3) {
                    opacity: 0;
                }
            }
        }
    }
    .header {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        min-height: 50px;
        background: rgba(black, .5);
        z-index: 1;
        &__container {
            width: 90%;
            margin: 0 auto;
            position: relative;
        }
        &__left {
            float: left;
            margin: .5em 0 .5em;
        }
        &__img {
            display: inline-block;
            vertical-align: middle;
            max-width: 30px;
        }
        &__title {
            display: inline-block;
            vertical-align: middle;
            color: #fff;
            font-family: "Oswald", sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 20px;
            line-height: 30px;
            @media(max-width: 650px) {
                max-width: 120px;
                font-size: 14px;
                line-height: 21px;
                text-align: left;
                margin-left: 15px;
            }
        }
    }
    .menu {
        float: right;
        display: block;
        padding-left: 0;
        li {
            display: inline-block;
            color: #fff;
            margin: 0 10px;
        }
        a {
            display: inline-block;
            color: #fff;
            text-decoration: none;
            font-family: 'Oswald', sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 18px;
            line-height: 27px;
        }
        &_tablet {
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: #000;
            margin-top: 0;
            padding: 10vh 0;
            li {
                display: block;
                text-align: center;
                margin: 0 auto;
                padding: 10px 0;
            }
        }
    }
</style>